.PrintPre {
    > * {
        font-size: 13px;
    }
    .HeaderPrint {
        padding-left: 0;
        margin-left: 0;
        line-height: 1.8;
        font-size: 11.5px;
    }
    .header-print {
        padding-left: 0;
        margin-left: 0;
        // padding-bottom: 0;
        // margin-bottom: 0;
        div {
            margin-left: 0;
            padding-left: 0;
        }
    }
    .print-title {
        font-weight: bold;
        font-size: 17px;
    }
    .info-cus {
        display: flex;
    }
    .print-w-50 {
        width: 50%;
    }
    .print-w-60 {
        width: 60%;
    }
    .print-w-40 {
        width: 40%;
    }
    .print-mw-30 {
        min-width: 30%;
    }
    .item-product {
        padding: 0;
        margin: 0;
        .title-product {
            padding-bottom: 2;
            margin-bottom: 2;
        }
    }
    .title-order {
        padding: 0;
        margin: 0;
    }
    .order-product {
        min-height: 90mm;
        font-size: 14px;
    }
    .phone-print {
        width: 20%;
    }
    .address-print {
        width: 75%;
    }
    .gender-print {
        width: 15%;
    }
    .weight-print {
        width: 15%;
    }
    .birthday-print {
        width: 35%;
    }
    .name-print {
        padding: 5px 0;
    }
    #PrintEtc {
        height: 209mm;
    }
    #PrintOtc {
        padding-top: 30px;
    }
}