@import "./Components/AccountingPrintingPreview/AccountingPrintingPreview.scss";
.SearchReceipt{
  height: 100%;
}
body{
  font-size: 100%;
}

.Transaction {
  .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link {
    color: $c-secondary;
  }
  .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
    font-weight: bold;
    border-color: $c-secondary $c-secondary white
  }
  .tab-content {
    .table-container {
      border-radius: 0;
      border-left: 1px solid;
    }
  }
  .navAcc {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .inputAcc {
    border: 1px solid #ced4da;
  }
  
  .form-group {
    padding: 1rem;
  }
  .p-1r{
    padding: 0 1rem 
  }
  .customerInfo {
    margin-top: 1rem;
  }
  .checkSum{
    display: flex;
    text-transform: uppercase;
  }
  
  .totalAll{
    position: relative;
    left: 120px;
  }
  .totalCash{
    position: relative;
    left: 150px;
  }
  .totalOther{
    position: relative;
    left: 180px;
  }
 
}
.divNull{
  display: block;
  width: 300px;
  height: 300px;
}
.info-form{
  position: relative;
  top: 20px;
}
.money-form{
  position: relative;
  top: 40px;
}
.dflex-center{
  display: flex;
  align-items: center;
}
.info-form{
  min-height: 450px;
  position: relative;
  
  
}
.library-book{
  display: flex;
  flex-direction: row;
}
.input-code{
  width: 60%;
}
.check-sum{
  position: relative;
  flex: 1 1 auto;
  padding: 1.5rem;
  .table-sum{
    border-radius: 5px;
    overflow: hidden;
    padding: 5px;
  }
}
  
.transaction-btn-cancel:hover {
  color: $c-secondary-hover;
  font-weight: bold;
}
.accouting-total {
  font-size: 1.2rem;
}