@import '../../Styles/variables';
.Inventory{
    .tableManager{
        .managerTable {
            display: block;
            empty-cells: show;
            border-spacing: 0;
            border: $border;       
            thead::-webkit-scrollbar-thumb {
            border-radius: 5px;
            }
            thead::-webkit-scrollbar {
            width: 5px;
            }
            thead {
            position: relative;
            display: block;
            // overflow-y: scroll;
            }
            tbody {
            position: relative;
            display: block;
            width: 100%;
            // overflow-y: scroll; 
            }
            
            td {
                line-height: 25px;
                padding:  0;
                font-size: 13px;
                border: $border;
                flex-basis: 100%;
                flex-grow: 2;
                display: block;
                padding-bottom: -30px;
            }
            td:first-child{
                flex-basis: 30%;
            }
            th:first-child{
                  flex-basis: 30%;
            }
            th.ID{
                flex-basis: 50%;
            }
            td.ID{
                flex-basis: 50%;
            }
            // th:last-child{
            //     flex-basis: 40%;
            // }
            // td:last-child{
            //     flex-basis: 40%;
                
            // }
            td.name{
                text-align: left;
                text-indent: 5px;
            }
            th {
            padding: 3px 0;
            font-size: 13px;
            border: $border;
            flex-basis: 100%;    
            flex-grow: 2;
            display: block;
            text-align: center;
            }  
            tr {
                width: 100%;
                display: flex;   
            }
            .bodyTable {
            // max-height: 300px;
            // min-height: 300px;
            text-align: center;
            .delete{
                margin: 2px 0;
                padding: 0px 5px;
                height: 30px;
            }
            }
            tbody::-webkit-scrollbar-thumb {
                border-radius: 5px;
            }
            tbody::-webkit-scrollbar {
                width: 5px;
            }
        }
    }
    .addNew{
        margin-bottom: 20px;
    }
}
.MedicineForm{  
    font-size: 14px;
    .row{
        padding: 0;
        margin: 0;
    } 
    .col-4{
        padding: 5px;
    }
    .col-8{
        padding: 5px;
    }
    .col-2{
        padding: 5px;
    }
    .warehouse_parts{
         padding: 5px 4px;     
        .searchBox{
            width: 200px;
        }
