        }
    }
}

.producerCreateControl{
    float: right;
    margin-right: 23px;
}
.ProducerForm{ 

    .btnCon{
        float: right;
        margin-right: 30px;
    }
}
.ProducerForm h1{
    margin: 20px 0 20px 0;
}
.ProducerForm textarea{
    text-indent: 17px;
    border-radius: 3px;
    border: solid 1px #c3c3c3;
    &:focus {
        outline-color: #007bff ;
    }
}
.transaction{
    padding-top: 50px;
    .transactionTable {
        display: block;
        empty-cells: show;
        border-spacing: 0;
        border: $border;       
        thead::-webkit-scrollbar-thumb {
        background: rgb(167, 161, 161);
        border-radius: 5px;
        }
        thead::-webkit-scrollbar {
        width: 5px;
        }
        thead {
        position: relative;
        display: block;
        // overflow-y: scroll;
        }
        tbody {
        position: relative;
        display: block;
        width: 100%;
        // overflow-y: scroll; 
        }
        td {
            line-height: 25px;
            padding:  0;
            font-size: 13px;
            border: $border;
            flex-basis: 100%;
            flex-grow: 2;
            display: block;
            padding-bottom: -30px;
        }
        td:first-child{
            flex-basis: 30%;
        }
        th:first-child{
              flex-basis: 30%;
        }
        th {
        padding: 3px 0;
        font-size: 13px;
        border: $border;
        flex-basis: 100%;    
        flex-grow: 2;
        display: block;
        text-align: center;
        }  
        th:last-child{
            flex-basis: 70%;
        }
        td:last-child{
            flex-basis: 70%;
        }
        th.amount{
            flex-basis: 50%;
        }
        td.amount{
            flex-basis: 50%;
        }
        th.batchName{
            flex-basis: 60%;
        }
        td.batchName{
            flex-basis: 60%;
        }
        tr {
            width: 100%;
            display: flex;   
        }
        .bodyTable {
        // max-height: 300px;
        // min-height: 300px;
        text-align: center;
        .delete{
            margin-top: -2px;
            padding: 0px 5px;
            height: 25px;
        }
        }
        tbody::-webkit-scrollbar-thumb {
            background: rgb(167, 161, 161);
            border-radius: 5px;
        }
        tbody::-webkit-scrollbar {
            width: 5px;
        }
    }
    .btnAdd{
        margin-bottom: 20px;
    }
    button{
        margin-right: 20px;
    }
    .btnOptions{
        text-align: right;
    }
    .menu {
        width: 100%;
        position: relative;
        background-color: $c-secondary;
        color: white;
        top: 0px;
        left: 0;
        z-index: 1000;
        >*{
            cursor: pointer;
            border-bottom: 1px solid white;
            padding: 5px;
        }
        >*:hover {
            background-color: $c-secondary-hover;
        }
    }
}
.FormLot{
    .btnControlLot{
        float: right;
        padding-top: 20px;
    }

    .menu {
        width: 100%;
        position: relative;
        background-color: $c-secondary;
        color: white;
        top: 0px;
        left: 0;
        z-index: 1000;
        >*{
            cursor: pointer;
            border-bottom: 1px solid white;
            padding: 5px;
        }
        >*:hover {
            background-color: $c-secondary-hover;
        }
    }
}
.auto-form-control{
    div{
        width: 100%;
        input{
            width: 100%;
            height: calc(1.5em + .75rem + 2px);
            padding: .375rem .75rem;
            font-size: 1rem;
            font-weight: 400;
            line-height: 1.5;
            color: #495057;
            background-color: #fff;
            background-clip: padding-box;
            border: 1px solid #ced4da;
            border-radius: .25rem;
            transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
        } 
    }
}

